import React from 'react';
import { Controller, Block } from '@ali/4ever-cangjie';
import { styledComponents, createHeadingId } from '@ali/4ever-bamboo';

interface IProps {
  controller: Controller;
  attributes: Record<string, any>;
  children: () => React.ReactChildren;
  node: Block;
}

// TODO 这里HOC Paragraph缺少必要types
export default function createHeading(Paragraph) {
  return function (props: IProps) {
    // TODO bugix 需要重新梳理props, 这里临时补上document, 解决在heading下渲染list的报错
    const { attributes, children, node, controller } = props;
    // @ts-ignore
    const Component = styledComponents(node.type);
    return (
      <Paragraph
        {...attributes}
        Component={Component}
        node={node}
        id={createHeadingId(node)}
        step={node.data.step}
        document={controller.value.document}
      >
        {children()}
      </Paragraph>
    );
  };
}
